<template>
  <div id="course-sum">
    <Table
      ModuleCode="ReportStatistics"
      ModuleName="课程统计"
      :Loading="Loading"
      :ShowExport="true"
      :ShowCreate="false"
      KeyFileld="CourseID"
      :Columns="Columns"
      :SortField.sync="SortField"
      :SortDir.sync="SortDir"
      :Datas.sync="Datas"
      :PageIndex.sync="PageIndex"
      :PageSize.sync="PageSize"
      :TotalCounts="TotalCounts"
      :SearchKey.sync="SKey"
      :SearchValue.sync="Key"
      :UnNeedEstateBtn="true"
      @LoadData="LoadData"
      @ExportHandle="ExportHandle"
      :UnNeedDelBtn="true"
      :ShowSelectColumn="false"
      @ViewRowHandle="View"
    >
      <template slot="ExSearch">
        <el-date-picker
          v-model="DateRange"
          clearable
          size="mini"
          type="daterange"
          align="right"
          range-separator="至"
          start-placeholder="任务开始日期"
          end-placeholder="任务结束日期"
          @change="ChangeData"
        ></el-date-picker
        >课程类型：
        <el-select
          v-model="value"
          placeholder="请选择"
          size="mini"
          @change="ChangeDate"
        >
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </template>
    </Table>
    <Evaluators
      :show.sync="visibles"
      :data="pdata"
      mCode="ReportStatistics"
    ></Evaluators>
    <!-- <Detail
      :show.sync="visibleB"
      :data="ddata"
      mCode="ReportStatistics"
      :ifback="back"
      @Close="Close"
    ></Detail>-->
  </div>
</template>
<script>
import api from "@/scripts/api";
import { FormatDate } from "@/scripts/utils";
export default {
  props: ["GetNum"],
  components: {
    Table: () => import("@/components/Table"),
    Evaluators: () => import("@/components/Eval/Evaluators")
    // Detail: () => import("@/components/Eval/Detail")
  },
  data() {
    return {
      Loading: false,
      visibles: false,
      // visibleB: false,
      canEdit: true,
      count: 0,
      back: true,
      pdata: "",
      ddata: "",
      Columns: [
        {
          Code: "CourseName",
          Header: "课程名称"
        },
        {
          Code: "TeacherName",
          Header: "教师名称"
        },
        {
          Code: "totalnum",
          Header: "被评数"
        },
        {
          Code: "avgscore",
          Header: "综合得分（百分制）",
          Sort: "custom"
        },
        {
          Code: "oprate",
          Header: "操作",
          Width: "120",
          NeedViewBtn: true,
          UnNeedEditBtn: true,
          UnNeedEstateBtn: true,
          UnNeedDelBtn: true,
          Slot: true,
          ShowBaseActionBtn: true
        }
      ],
      Datas: [
        {
          CourseName: "人工智能",
          tName: "marry",
          VoteNumber: 5,
          highest: 86,
          lowestPoints: 78,
          score: 86
        }
      ],
      PageIndex: 1,
      PageSize: 20,
      TotalCounts: 0,
      Key: "",
      SKey: "",
      SortField: "",
      SortDir: 0,
      Estate: "",
      DateRange: [
        new Date(Date.now() - 8.64 * Math.pow(10, 7) * 30),
        new Date()
      ],
      options: [
        { label: "合格课程", value: 1 },
        { label: "不合格课程", value: 2 }
      ],
      value: ""
    };
  },
  mounted() {},
  watch: {
    GetNum(val) {
      if (val > 0) {
        this.LoadData(0);
      }
    }
  },
  methods: {
    // 进入评价人列表
    async View(r) {
      this.visibles = true;
      this.pdata = r;
    },
    // 进入评价明细
    // ShowD(d) {
    //   console.log('jsdfk', d);
    //   // this.visibleB = true;
    //   // this.ddata = d;
    // },
    Close() {
      this.visibles = false;
    },
    async LoadData(state) {
      this.Datas = [];
      this.Loading = true;
      const res = await api.EVALPlan.GetReportStatisticDatas(
        2,
        "",
        FormatDate(this.DateRange[0], "yyyy-MM-dd"),
        FormatDate(this.DateRange[1], "yyyy-MM-dd"),
        state,
        0,
        this.PageIndex,
        this.PageSize,
        this.SortField,
        this.SortDir
      );
      if (res && res.Datas && res.Datas.length > 0) {
        res.Datas.forEach(ele => {
          ele.jgl = Number(ele.jgl).toFixed(2) * 100 + "%";
          ele.avgscore = Number(ele.avgscore).toFixed(2);
        });
        this.Datas = res.Datas;
        this.TotalCounts = res.rowsCount;
      } else {
        this.Datas = [];
        this.wData = [];
        this.TotalCounts = 0;
      }
      this.Loading = false;
    },
    ChangeDate(day) {
      this.PageIndex = 1;
      this.PageSize = 20;
      this.LoadData(day);
    },
    ChangeData() {
      this.PageIndex = 1;
      this.PageSize = 20;
      this.LoadData();
      this.$emit("CourseTotal");
    },
    async ExportHandle(callback) {
      const res = await api.EVALPlan.ExportData(
        2,
        "",
        FormatDate(this.DateRange[0], "yyyy-MM-dd"),
        FormatDate(this.DateRange[1], "yyyy-MM-dd"),
        0,
        0
      );
      if (res) {
        const fileDownload = require("js-file-download");
        const fileName = `课程统计.xls`;
        fileDownload(res, fileName);
      }
      callback();
    }
  }
};
</script>
<style lang="less">
#course-sum {
  .el-table .el-table__body tbody tr td {
    padding: 7px;
  }
  .search .el-select--mini .el-input__inner {
    width: 220px;
  }
  .tbl .handle_bar .search .el-range-editor {
    margin-left: 0px;
  }
}
</style>
