<template>
  <div class="teacher-class-list" shadow="never">
    <div class="flex-between list-head">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item replace to="/main/teacherSpace/courseManage/courseList">
          {{course["CourseName"]}}
        </el-breadcrumb-item>
        <el-breadcrumb-item>班级管理</el-breadcrumb-item>
      </el-breadcrumb>
      <div>
        <el-button type="primary" size="mini" @click="searchData">刷新</el-button>
        <el-button type="primary" size="mini" @click="open()">添加班级</el-button>
      </div>
    </div>
    <div class="card-warp" :class="{'mini':IsMINI}" v-loading="loading">
      <el-card shadow="hover" v-for="(item,index) in dataList" :key="index">
        <div class="card-hd">
          <el-tooltip v-if="item.Key['ClassName'].length>8" :content="item.Key['ClassName']" placement="top">
            <span>{{item.Key['ClassName']}}</span>
          </el-tooltip>
          <span v-else>{{item.Key['ClassName']}}</span>
          <i class="el-icon-delete" @click="dataDrop(item)"></i>
        </div>
        <div class="card-btns">
          <el-button type="primary" size="mini" @click="open(item)">编辑</el-button>
          <el-button type="success" size="mini" @click="dataView(item)">人数：{{item["Total"]}}</el-button>
        </div>
      </el-card>
    </div>

    <el-dialog center :title="`${mode === 'edit' ? '班级修改' : '创建班级'}`" :close-on-click-modal="false"
               :visible.sync="dialogVisible" :modal-append-to-body="false">
      <el-form label-position="right" :model="form" :rules="rules" ref="form" label-width="130px" size="mini"
               status-icon @keyup.enter.native="saveData">
        <el-row>
          <el-col>
            <el-form-item label="班级名称：" prop="className">
              <el-input type="text" v-model="form.className"/>
            </el-form-item>
            <el-form-item label="允许加入：">
              <el-switch v-model="form.allowJoin"></el-switch>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer">
        <el-button-group>
          <el-button type="primary" icon="el-icon-check" size="mini" @click="saveData" autofocus>
            {{mode === 'edit' ? '提交修改' : '立即创建'}}
          </el-button>
          <el-button icon="el-icon-close" size="mini" @click="close">取 消</el-button>
        </el-button-group>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import api from "@/scripts/api";
import { setSession, getSession } from "@/scripts/utils";

export default {
  data () {
    return {
      IsMINI: false,
      courseID: getSession("campus_CourseID"),
      course: getSession("campus_Course"),
      loading: false,
      dataList: [],
      mode: "", // add, edit
      dialogVisible: false,
      form: {
        courseID: getSession("campus_CourseID"),
        className: "",
        classID: "",
        allowJoin: true
      },
      rules: {
        courseName: {
          required: true,
          min: 1,
          max: 50,
          message: "长度在 1 到 50 个字符",
          trigger: "blur"
        }
      },
      btnLoading: false
    };
  },
  created () {
    this.searchData();
  },
  methods: {
    searchData () {
      this.dataList = [];
      this.loading = true;
      api.IClass.DataList(this.courseID).then(res => {
        if (res.Result && res.Datas && res.Datas.length > 0) {
          res.Datas.sort(r => r["CreateTime"]).reverse();
          res.Datas.forEach(item => {
            this.dataList.push(item);
          });
        }
        this.loading = false;
      }).catch(() => {
        this.loading = false;
      });
    },
    dataDrop (item) {
      this.$confirm("此操作将删除该班级, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.dataDelete(item);
      }).catch(() => {
      });
    },
    dataDelete (item) {
      api.IClass.DataDelete(
        item.Key["ClassID"]
      ).then(res => {
        if (res.Result) {
          this.$message({
            message: "删除成功！",
            type: "success"
          });
          setTimeout(() => {
            this.searchData();
          }, 100);
        }
      }).catch(() => {
        this.$message({
          showClose: true,
          message: "操作失败",
          type: "error"
        });
      });
    },
    dataView (item) {
      setSession("campus_Class", item);
      setSession("campus_ClassID", item.Key["ClassID"]);
      this.$router.push("/p/student");
    },
    // 弹窗相关
    open (form) {
      // 修改-原始数据, 添加-null
      if (form) {
        this.form.className = form.Key["ClassName"];
        this.form.classID = form.Key["ClassID"];
        this.form.allowJoin = !!form.Key["AllowJoin"];
        this.mode = "edit";
      } else {
        this.form.className = "";
        this.form.classID = "";
        this.mode = "add";
      }
      this.dialogVisible = true;
    },
    close () {
      this.form = {};
      this.dialogVisible = false;
    },
    saveData (form) {
      this.$refs.form.validate(valid => {
        if (valid) {
          this.btnLoading = true;
          api.IClass.TeacherDataSave(
            this.form.classID, /// classID
            this.form.className || "", // className
            this.form.courseID, // courseID
            this.form.allowJoin ? "1" : "0"
          ).then(res => {
            if (res.Result) {
              this.$message({
                message: "操作成功！",
                type: "success"
              });
              this.dialogVisible = false;
              this.searchData();
            } else if (res.Info) {
              this.$message.error(res.Info);
            }
            this.btnLoading = false;
          }).catch(() => {
            this.$message.error("操作失败");
            this.btnLoading = false;
          });
        } else {
          this.$message({
            showClose: true,
            message: "请输入正确格式",
            type: "error"
          });
          return false;
        }
      });
    },
    // 调整
    AdjustSize () {
      const width = parseInt(window.innerWidth);
      this.IsMINI = width < 1600;
    }
  }
};
</script>
<style lang="less">
.teacher-class-list {
  width: 100%;

  .flex-between {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .card-warp {
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    gap: @Margin;
    height: calc(100% - 66px);

    .el-card {
      height: 100px;
      width: 100%;
      box-sizing: border-box;

      .el-card__body {
        padding: 10px;
      }

      .card-hd {
        .flex-between();
        line-height: 35px;
        font-size: 16px;
        color: #333333;

        span {
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }

        i.el-icon-delete {
          color: #999999;
          cursor: pointer;
          font-size: 17px;
        }
      }

      .card-btns {
        .flex-between();
        margin-top: 15px;

        .el-button {
          width: 80px;
          height: 28px;
          border-radius: 3px !important;
        }
      }
    }
  }

  .card-warp.mini {
    grid-template-columns: repeat(6, 1fr);
  }
}
</style>
