<template>
  <div class="dtrange">
    <el-date-picker
      size="mini"
      v-model="date"
      :picker-options="datepickerOptions"
      :clearable="false"
      :editable="false"
      @change="val=>DateTimeRangeChange(val,'Date')"
    />
    <el-time-picker
      size="mini"
      v-model="times1"
      format="HH:mm"
      :clearable="false"
      :editable="false"
      placeholder="开始时间"
      :picker-options="{selectableRange: `${IsToday() ? FormatTime(new Date()) : '00:00:00' }-23:59:59` }"
      @change="val=>DateTimeRangeChange(val,'TimeStart')"
    />
    <span>至</span>
    <el-time-picker
      size="mini"
      v-model="times2"
      format="HH:mm"
      :clearable="false"
      :editable="false"
      placeholder="结束时间"
      :picker-options="{ selectableRange: `${ FormatTime(times1,10)}-23:59:59` }"
      @change="val=>DateTimeRangeChange(val,'TimeEnd')"
    />
  </div>
</template>

<script>
import moment from "moment";
export default {
  props: {
    /**
     * 不能早以当前时间
     */
    NotEralier: {
      type: Boolean,
      default: true
    },
    // 日期
    TimeStart: {
      type: Date,
      default: () => new Date()
    },
    // 日期
    TimeEnd: {
      type: Date,
      default: () =>
        new Date(
          moment(new Date())
            .add(30, "m")
            .format("YYYY-MM-DD HH:mm:ss")
        )
    }
  },
  watch: {},
  data() {
    const $this = this;
    return {
      date: new Date(),
      times1: this.TimeStart,
      times2: this.TimeEnd,
      expand: this.Expand,
      showMore: "",
      datepickerOptions: {
        firstDayOfWeek: this.FirstDayOfWeek,
        disabledDate(time) {
          if ($this.NotEralier) {
            return time.getTime() < moment(new Date()).add(-1, "days");
          } else {
            return true;
          }
        }
      }
    };
  },
  mounted() {
    // this.DateTimeRangeChange();
  },
  methods: {
    IsToday() {
      return moment(this.date).isSame(new Date(), "day");
    },
    FormatTime(val, m = 0) {
      return moment(val)
        .add(m, "m")
        .format("HH:mm:ss");
    },
    DateTimeRangeChange(val, type) {
      switch (type) {
        case "Date": {
          if (this.IsToday()) {
            this.times1 = new Date();
            this.times2 = new Date(
              moment(this.times1)
                .add(30, "m")
                .format("YYYY-MM-DD HH:mm:ss")
            );
          }
          break;
        }
        case "TimeStart": {
          if (this.times2 < this.times1) {
            this.times2 = new Date(
              moment(this.times1)
                .add(30, "m")
                .format("YYYY-MM-DD HH:mm:ss")
            );
          }
          break;
        }
        case "TimeEnd": {
        }
      }

      this.$emit(
        "update:TimeStart",
        new Date(
          `${moment(this.date).format("YYYY-MM-DD")} ${moment(
            this.times1
          ).format("HH:mm:ss")}`
        )
      );
      this.$emit(
        "update:TimeEnd",
        new Date(
          `${moment(this.date).format("YYYY-MM-DD")} ${moment(
            this.times2
          ).format("HH:mm:ss")}`
        )
      );
      this.$emit("change");
    }
  }
};
</script>

<style lang="less">
.dtrange {
  .el-date-editor + .el-date-editor {
    margin-left: 10px;
  }

  .el-date-editor {
    width: 120px;
  }

  > span {
    margin: auto 5px;
  }
}
</style>