<template>
  <div class="edit-file-wrap">
    <el-form
      v-loading="SyncLoading"
      :disabled="SyncLoading"
      class="file-form"
      ref="form"
      :model="form"
      :rules="formRule"
      label-width="100px"
      label-position="right"
      size="mini"
      status-icon
    >
      <el-form-item label-width="0px" prop="FileID">
        <div class="upload-box">
          <template v-if="uploadStep === 0">
            <div class="btns">
              <el-upload
                ref="upload"
                :action="uploadUrl"
                :class="{ limit: form.fileList.length >= fileLimit }"
                :limit="fileLimit"
                :file-list="form.fileList"
                :auto-upload="false"
                :on-change="UploadChange"
                :on-remove="RemoveHandle"
                :on-success="UploadSuccess"
                :accept="acceptType"
                v-loading="uploadLoding"
                :on-progress="UploadProgress"
                element-loading-text="文件上传中"
              >
                <el-button
                  class="btn-upload"
                  type="primary"
                  icon="iconfont icon_yunupload"
                  slot="trigger"
                  size="small"
                  >本地上传</el-button
                >
              </el-upload>
              <el-button
                class="btn-upload ml80"
                type="success"
                icon="iconfont icon_yunupload"
                slot="trigger"
                size="small"
                v-if="form.fileList.length > 0"
                @click="Upload"
                >上传到服务器</el-button
              >
              <el-button
                v-else
                class="btn-upload ml80"
                type="primary"
                icon="iconfont el-icon-folder-checked"
                slot="trigger"
                size="small"
                @click="yunUpload"
                >网盘选择</el-button
              >
            </div>
            <p class="p">请选择上传{{ title }}的方式</p>
            <p class="tip">本地上传比较慢，建议从网盘上传</p>
          </template>
          <template v-if="uploadStep === 1">
            <i class="status el-icon-upload2"></i>
            <el-progress
              :stroke-width="8"
              :percentage="uploadProgress"
            ></el-progress>
            <p class="p1">正在上传中...</p>
            <p class="tip">文件正在上传中，请稍等</p>
            <div class="btns-0">
              <el-button type="primary" @click="CancelUpload"
                >取消上传</el-button
              >
            </div>
          </template>
          <template v-if="uploadStep === 2">
            <i class="el-icon-success"></i>
            <p class="p2">上传成功</p>
            <p class="tip">文件上传成功，点击下方按钮进行操作</p>
            <div class="btns-1">
              <el-button
                type="primary"
                icon="el-icon-view"
                @click="viewSource(type)"
                >预览文件</el-button
              >
              <el-button type="primary" icon="el-icon-sort" @click="BackUpload"
                >替换文件</el-button
              >
            </div>
          </template>
          <template v-if="uploadStep === 3">
            <i class="el-icon-error"></i>
            <p class="p2">上传失败</p>
            <p class="tip">{{ ErrInfo }}</p>
            <div class="btns-0">
              <el-button type="primary" icon="el-icon-back" @click="BackUpload"
                >返回</el-button
              >
            </div>
          </template>
        </div>
      </el-form-item>
      <el-form-item label="标题名称：" prop="BPLName">
        <el-input
          v-model="form.BPLName"
          class="name-input"
          maxlength="60"
          clearable
          show-word-limit
          placeholder="请输入名称"
        ></el-input>
      </el-form-item>
      <el-form-item label="图片切换：" v-if="type === 'doc'">
        <el-input-number :min="3" :max="100" :precision="0"></el-input-number>
        <em class="t">秒</em>
      </el-form-item>
      <el-form-item label="循环模式：" prop="BPLLoopMode">
        <el-radio-group v-model="form.BPLLoopMode" class="timing-radio">
          <el-radio :label="0">
            <span class="text">循环次数</span>
            <el-input-number
              :min="0"
              :max="10"
              :precision="0"
              v-model="form.BPLLoopCount"
            ></el-input-number>
            <em class="t">次</em>
            <em class="t" v-if="form.BPLLoopCount > 1">，循环间隔：</em>
            <el-input-number
              :min="0"
              :max="100"
              :precision="0"
              v-model="form.BPLLoopInterval"
              v-if="form.BPLLoopCount > 1"
            ></el-input-number>
            <em class="t" v-if="form.BPLLoopCount > 1">秒</em>
          </el-radio>
          <el-radio :label="1">
            <span class="text">播放时长</span>
            <el-time-picker
              v-model="form.BPLDuration"
              value-format="HH:mm:ss"
              :picker-options="{ selectableRange: '00:00:00 - 23:00:00' }"
              placeholder="请选择播放时长"
            ></el-time-picker>
          </el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item
        label="播放音量："
        class="mb0"
        v-if="type === 'video' || 'audio'"
        prop="BPLVolume"
      >
        <div class="block">
          <span class="iconfont icon_volume"></span>
          <el-slider
            v-model="form.BPLVolume"
            :min="0"
            :max="15"
            :step="1"
          ></el-slider>
          <span class="tip">当前音量{{ form.BPLVolume }}</span>
        </div>
      </el-form-item>
    </el-form>
    <YunFile
      :show.sync="yunVisible"
      :title="title"
      :type="type"
      @OnChooseFile="OnChooseFile"
    ></YunFile>
    <!-- 视频预览弹窗 -->
    <el-dialog
      custom-class="video-view"
      :title.sync="form.BPLName"
      :visible.sync="videoVisible"
      :modal-append-to-body="false"
      :close-on-click-modal="false"
      :before-close="onVideoViewClose"
      :destroy-on-close="true"
      center
    >
      <PlayBox ref="PlayBox" :Videos.sync="videoList" :ShowPlayList="false" />
    </el-dialog>
    <!-- 语音预览弹窗 -->
    <el-dialog
      custom-class="audio-view"
      :title.sync="form.BPLName"
      :visible.sync="audioVisible"
      :modal-append-to-body="false"
      :close-on-click-modal="false"
      :before-close="onAudioViewClose"
      :destroy-on-close="true"
      center
    >
      <audio
        ref="audio"
        autoplay
        :src="audioUrl"
        controls
        controlslist="nodownload"
      >
        您的浏览器不支持 audio 标签。
      </audio>
    </el-dialog>
  </div>
</template>

<script>
import api from "@/scripts/api";
import store from "@/scripts/store";
import { MakeDurationToSeconds, FormatDuration } from "@/scripts/utils";
export default {
  components: {
    YunFile: () => import("@/components/IPB/YunFile"),
    PlayBox: () => import("@/components/Player/PlayBox"),
  },
  props: {
    title: {
      type: String,
      default: "视频",
    },
    type: {
      type: String,
      default: "video",
    },
  },
  data() {
    // 验证名称
    // const ValidateName = async (rule, name, callback) => {
    //   const res = await api.BP.ValidatePlanItemName(
    //     this.$route.params.bpid,
    //     name,
    //     this.$route.params.id
    //   );
    //   if (res.Result) {
    //     callback();
    //   } else {
    //     callback(new Error(res.Info));
    //   }
    // };
    return {
      SyncLoading: false,
      ErrInfo: "",
      uploadUrl: "",
      key: "",
      uploadStep: 0,
      fileLimit: 1,
      uploadLoding: false,
      uploadProgress: 0,
      yunVisible: false,
      videoVisible: false,
      videoList: [],
      audioVisible: false,
      audioUrl: "",
      MaterialStorePath: "",
      form: {
        FileID: null,
        fileList: [],
        BPLName: "",
        loopInterval: 0,
        BPLLoopMode: 0,
        BPLLoopCount: 1,
        BPLDuration: "00:00:00",
        BPLVolume: 7,
        BPLTime: "",
        BPLLoopInterval: 1,
      },
      formRule: {
        FileID: [
          {
            required: true,
            message: "请选择素材文件",
            trigger: "blur",
          },
        ],
        BPLLoopMode: [
          {
            required: true,
            message: "请选择循环模式",
            trigger: "blur",
          },
        ],
        BPLLoopInterval: [
          {
            required: true,
            message: "请选择循环间隔",
            trigger: "blur",
          },
        ],
        BPLName: [
          {
            required: true,
            message: "请输入标题名称",
            trigger: "blur",
            // validator: ValidateName,
          },
        ],
      },
    };
  },
  mounted() {
    var bplid = this.$route.params.bplid;
    if (bplid) {
      this.LoadData();
    }
    const config = store.GetConfigInfo();
    if (config) {
      const userInfo = store.GetUserInfo();
      this.uploadUrl = `http://${config.Host}:${config.Port}/FJRH.Campus/UploadIPBFile?uid=${userInfo.UserID}`;
    }
  },
  computed: {
    acceptType() {
      switch (this.$route.params.type) {
        case "video":
          return "video/*";
        case "audio":
          return "audio/*";
        case "doc":
          return ".doc,.docx,application/msword,application/vnd.openxmlformats-officedocument.wordprocessingml.document";
        default:
          return "video/*";
      }
    },
  },
  methods: {
    Confirm() {
      this.SyncLoading = true;
      this.$refs.form.validate(async (valid) => {
        if (valid) {
          let type = 1;
          switch (this.type) {
            case "video":
              type = 1;
              break;
            case "audio":
              type = 2;
              break;
            case "doc":
              type = 4;
              break;
          }
          var time = `${MakeDurationToSeconds(this.form.BPLDuration)}`;
          const res = await api.BP.AddPlanItem(
            this.$route.params.bplid,
            this.$route.params.bpid,
            this.form.BPLName,
            type,
            this.form.FileID,
            this.form.BPLLoopMode,
            this.form.BPLLoopInterval,
            this.form.BPLLoopCount,
            time,
            null,
            3,
            null,
            this.form.BPLVolume
          );

          if (res && res.Result) {
            this.$emit("ConfirmCompleted");
          }

          this.SyncLoading = false;
        } else {
          this.SyncLoading = false;
        }
      });
    },
    async LoadData() {
      const res = await api.BP.GetBroadcastPlan(this.$route.params.bplid);
      if (res && res.Result) {
        this.form.BPLName = res.Data.BPLName;
        this.form.FileID = res.Data.BPLRelationID;
        this.form.BPLLoopMode = res.Data.BPLLoopMode;
        this.form.BPLLoopInterval = res.Data.BPLLoopInterval;
        this.form.BPLLoopCount = res.Data.BPLLoopCount;
        this.form.BPLDuration = `${FormatDuration(res.Data.BPLDuration)}`;
        this.form.BPLVolume = res.Data.BPLVolume;
      }
    },
    yunUpload() {
      this.yunVisible = true;
    },
    /**
     * 文件状态改变时的钩子，添加文件、上传成功和上传失败时都会被调用
     */
    UploadChange(file, fileList) {
      console.log(file);
      this.form.fileList = fileList;
    },
    CancelUpload() {
      this.$refs.upload.abort();
    },
    UploadSuccess(response, file, fileList) {
      if (response.Result) {
        this.uploadStep = 2;
        this.form.FileID = response.ID;
        this.form.BPLName = file.name;
      } else {
        this.uploadStep = 3;
        this.ErrInfo = file.response.Info;
      }
    },
    OnChooseFile(file) {
      this.uploadStep = 2;
      this.form.FileID = file[0].MaterialID;
      this.form.BPLName = file[0].MaterialName;
    },
    UploadProgress(event, file, fileList) {
      this.uploadProgress = event.percent;
    },
    async Upload() {
      if (this.form.fileList && this.form.fileList.length > 0) {
        this.uploadProgress = 0;
        this.ErrInfo = "";
        this.uploadStep = 1;
        this.$refs.upload.submit();
      }
    },
    BackUpload() {
      this.uploadStep = 0;
      this.$nextTick(() => {
        this.$refs.upload.clearFiles();
        this.form.fileList = [];
        this.form.FileID = "";
        this.form.BPLName = "";
      });
    },
    /**
     * 文件列表移除文件时的钩子
     */
    RemoveHandle(file, fileList) {
      this.form.fileList = fileList;
    },
    // 预览文件
    viewSource(type) {
      if (type === "video") {
        this.videoVisible = true;
        this.videoList = [{ Path: `/IPB/${this.form.FileID}.mp4` }];
      } else if (type === "audio") {
        this.audioVisible = true;
        this.audioUrl = `http://${this.$root.SrvConfigInfo.LiveHost}:${this.$root.SrvConfigInfo.LivePort}/vod/IPB/${this.form.FileID}.mp3`;
      }
    },
    changeFile() {},
    onVideoViewClose() {
      this.videoList = [];
      if (this.$refs.PlayBox) {
        this.$refs.PlayBox.Dispose();
      }
      this.$nextTick(() => {
        this.videoVisible = false;
      });
    },
    onAudioViewClose() {
      this.audioUrl = "";
      this.audioVisible = false;
    },
  },
};
</script>

<style lang="less">
.edit-file {
  .upload-box {
    margin-bottom: 20px;
    width: 640px;
    height: 360px;
    border-radius: 4px;
    border: 1px solid @ThemeColor;
    text-align: center;

    .el-upload-list__item-name {
      white-space: initial;
    }

    .btns,
    .btns-1,
    .btns-0 {
      display: grid;
      grid-template-columns: repeat(2, 50%);
      justify-content: center;
      justify-items: center;
      margin-top: 70px;

      .ml80 {
        width: 120px;
        height: 120px;
      }
    }
    .btns-0 {
      grid-template-columns: repeat(1, 100%);
    }
    .btns-1 {
      margin: auto;
      width: 60%;
      margin-top: 40px;
    }
    .btn-upload.el-button--success {
      padding-left: 0px;
      padding-right: 0px;
    }
    .btn-upload {
      padding: 32px 31px;
      border-radius: 4px !important;
      .iconfont {
        display: block;
        font-size: 24px;
      }
      > span {
        display: block;
        font-size: 14px;
        margin-left: 0;
        margin-top: 16px;
      }
    }
    .p {
      font-size: 14px;
      line-height: 28px;
      margin-top: 32px;
    }
    .tip {
      color: #999;
      font-size: 12px;
      line-height: 18px;
    }
    .el-icon-upload2 {
      width: 86px;
      height: 86px;
      line-height: 86px;
      margin-top: 50px;
      border-radius: 50%;
      font-size: 45px;
      color: #fff;
      background-color: @ThemeColor;
    }
    .el-icon-success,
    .el-icon-error {
      margin-top: 50px;
      font-size: 90px;
    }
    .el-icon-success {
      color: @GreenColor;
    }
    .el-icon-error {
      color: @RedColor;
    }
    .el-progress {
      width: 340px;
      margin: 20px auto 0;
    }
    .el-progress__text {
      display: none;
    }
    .p1,
    .p2 {
      margin-top: 12px;
      font-size: 24px;
      line-height: 40px;
    }
    .p2 {
      margin-top: 6px;
    }
  }

  .el-radio-group {
    width: 250px;
    .el-radio {
      float: left;
    }
  }
}
</style>