<template>
  <div>
    <el-form
      class="edit-file-wrap file-form"
      ref="form"
      :model="form"
      :rules="formRule"
      label-width="95px"
      label-position="left"
      size="mini"
      status-icon
    >
      <el-form-item label="直播名称：" prop="PSName">
        <el-input
          v-model="form.PSName"
          class="name-input mr10"
          maxlength="30"
          clearable
          show-word-limit
          placeholder="请输入直播名称"
        ></el-input>
        <el-button type="primary" size="mini">点击预览</el-button>
        <el-button type="primary" size="mini" @click="chooseAdd"
          >选择地址</el-button
        >
      </el-form-item>
      <el-form-item label="直播地址：" prop="PSURL">
        <el-input
          v-model="form.PSURL"
          class="name-input mr10"
          maxlength="150"
          clearable
          show-word-limit
          placeholder="请输入直播地址"
        ></el-input>
      </el-form-item>
      <el-form-item label="播放时长：" prop="duration">
        <el-time-picker
          class="live-duration"
          v-model="form.duration"
          value-format="HH:mm:ss"
          :picker-options="{ selectableRange: '00:00:00 - 23:00:00' }"
          placeholder="请选择播放时长"
        ></el-time-picker>
      </el-form-item>
      <el-form-item label="播放音量：" class="mb0">
        <div class="block">
          <span class="iconfont icon_volume"></span>
          <el-slider
            v-model="form.volume"
            :min="0"
            :max="15"
            :step="1"
          ></el-slider>
          <span class="tip">当前音量{{ form.volume }}</span>
        </div>
      </el-form-item>
    </el-form>
    <el-dialog
      center
      title="直播地址列表"
      :visible.sync="addrVisible"
      :modal-append-to-body="false"
      :close-on-click-modal="false"
      width="800px"
    >
      <SubTable
        :Loading="Loading"
        :Subbutton="false"
        :ShowExport="false"
        :Columns="Columns"
        :Datas.sync="Datas"
        :selections.sync="selects"
        :ShowPage="true"
        :singleChoose="true"
        @LoadData="LoadData"
      ></SubTable>
      <div slot="footer">
        <el-button-group>
          <el-button
            type="primary"
            icon="el-icon-check"
            size="mini"
            @click="cfmChoose"
            autofocus
            native-type="submit"
            >确 定</el-button
          >
          <el-button icon="el-icon-close" size="mini" @click="visible = false"
            >取 消</el-button
          >
        </el-button-group>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import api from "@/scripts/api";
import { MakeDurationToSeconds, FormatDuration } from "@/scripts/utils";
export default {
  components: {
    SubTable: () => import("@/components/SubTable"),
  },
  data() {
    const ValidateDuration = (rule, duration, callback) => {
      if (duration && duration !== "00:00:00") {
        callback();
      } else {
        callback(new Error("请设置播放时长"));
      }
    };
    return {
      isEdit: true,
      form: {
        duration: "00:00:00",
        PSName: "",
        PSURL: "",
        volume: 7,
      },
      formRule: {
        PSName: [
          {
            required: true,
            message: "请输入直播名称",
            trigger: "blur",
          },
        ],
        PSURL: [
          {
            required: true,
            message: "请输入直播地址",
            trigger: "blur",
          },
        ],
        duration: [
          {
            required: true,
            trigger: "blur",
            validator: ValidateDuration,
          },
        ],
      },
      addrVisible: false,
      Loading: false,
      Datas: [],
      Columns: [
        {
          Code: "PSName",
          Header: "直播地址名称",
          Fixed: "left",
        },
        {
          Code: "PSURL",
          Header: "直播地址",
        },
      ],
      selects: null,
    };
  },
  mounted() {
    var bplid = this.$route.params.bplid;
    if (bplid) {
      this.LoadData();
    }
  },
  methods: {
    editorChange(value) {
      this.form.content = value;
    },
    async chooseAdd() {
      this.addrVisible = true;
      const res = await api.ProgramSource.GetDatas("", 1, 9999, "PSName", 0);
      if (res && res.Datas) {
        this.Datas = res.Datas;
      }
    },
    async LoadData() {
      const res = await api.BP.GetBroadcastPlan(this.$route.params.bplid);
      if (res && res.Result) {
        this.form.PSName = res.Data.BPLName;
        this.form.PSURL = res.Data.BPLURL;
        this.form.duration = `${FormatDuration(res.Data.BPLDuration)}`;
        this.form.volume = res.Data.BPLVolume;
      }
    },
    cfmChoose() {
      this.form.PSName = this.selects[0].PSName;
      this.form.PSURL = this.selects[0].PSURL;
      this.addrVisible = false;
    },
    async Confirm() {
      this.$refs.form.validate(async (valid) => {
        if (valid) {
          var time = `${MakeDurationToSeconds(this.form.duration)}`;
          const res = await api.BP.AddPlanItem(
            this.$route.params.bplid,
            this.$route.params.bpid,
            this.form.PSName,
            6,
            null,
            1,
            0,
            0,
            time,
            null,
            3,
            null,
            this.form.volume,
            this.form.PSURL
          );
          if (res && res.Result) {
            this.$emit("ConfirmCompleted");
          } else {
            var msg = "";
            if (res.Info) {
              res.Info.forEach((element) => {
                msg += element + "\r\n";
              });
            } else {
              msg = "添加失败";
            }
            this.$message({
              message: msg,
              type: "warning",
              duration: 1000,
            });
          }
        }
      });
    },
  },
};
</script>

<style lang="less">
.file-form {
  .mr10 {
    margin-right: 10px;
  }
  .live-duration {
    width: 540px !important;
  }
}
</style>