<template>
  <div class="dr-tree">
    <div class="dr-head" v-if="addbtn">
      <div class="add-btn">
        <el-button
          type="primary"
          icon="el-icon-plus"
          @click="addGauge"
          size="mini"
          >添加列表</el-button
        >
      </div>
    </div>
    <div class="box">
      <div class="first-node" v-for="(item, i) in trees" :key="i">
        <div class="first-node-head">
          <div class="title">
            {{ item.label }}
          </div>
          <div class="opts" v-show="i >0">
            <i class="el-icon-delete" @click="titleRemove(i)"></i>
          </div>
        </div>
        <el-collapse-transition>
          <div class="dr-container" v-show="!item.isFold">
            <el-form
              size="mini"
              label-suffix=":"
              label-width="80px"
              :disabled="!addbtn"
            >
              <div
                class="second-node"
                v-for="(sub, j) in item.children"
                :key="j"
              >
                <el-row>
                  <el-form-item label="维修配件">
                    <el-select
                      filterable
                      v-model="sub.dr_pj"
                      placeholder="请选择维修配件"
                    >
                      <el-option
                        v-for="o in pjOptions"
                        :key="o.value"
                        :label="o.label"
                        :value="o.value"
                      ></el-option>
                    </el-select>
                  </el-form-item>
                  <el-form-item label="维修方式">
                    <el-select
                      filterable
                      v-model="sub.dr_method"
                      placeholder="请选择维修方式"
                    >
                      <el-option
                        v-for="o in RepailWaysoptions"
                        :key="o.value"
                        :label="o.label"
                        :value="o.value"
                      ></el-option>
                    </el-select>
                  </el-form-item>
                  <el-form-item label="维修费用">
                    <el-input-number
                      v-model="sub.dr_money"
                      :min="0"
                      :max="9999999"
                      :precision="0"
                    ></el-input-number>
                  </el-form-item>
                </el-row>
                <el-row>
                  <el-col>
                    <el-form-item label="维修单位">
                      <el-input
                        type="textarea"
                        rows="2"
                        v-model="sub.dr_unit"
                        clearable
                      ></el-input>
                    </el-form-item>
                  </el-col>
                  <el-col>
                    <el-form-item label="维修备注">
                      <el-input
                        type="textarea"
                        rows="2"
                        v-model="sub.dr_demo"
                        clearable
                      ></el-input>
                    </el-form-item>
                  </el-col>
                </el-row>
              </div>
            </el-form>
          </div>
        </el-collapse-transition>
      </div>
    </div>
    <div class="dr-head">
      <div class="add-btn">
        <el-button
          :loading="SyncLoading"
          type="primary"
          icon="el-icon-check"
          size="mini"
          @click="SaveGauge"
          v-if="trees.length > 0 && addbtn"
          >保 存</el-button
        >
      </div>
    </div>
  </div>
</template>
<script>
import api from "@/scripts/api";
export default {
  props: ["trees", "pjOptions", "canAdd", "drid", "addbtn"],
  data() {
    return {
      RepailWaysoptions: [],
      UnitOptions: [],
      SyncLoading: false
    };
  },
  mounted() {
    this.RepailWaysoptions = [];
    this.getMetadatas("RepailWays", this.RepailWaysoptions);
  },
  methods: {
    // 获取元素据列表
    async getMetadatas(code, opts) {
      const res = await api.Metadata.GetTrees(code);
      if (!res.Result) {
        this.$message({
          showClose: true,
          message: res.message,
          type: res.type
        });
      } else {
        res.Datas.forEach(element => {
          if (element.MetadataCode !== code) {
            opts.push({
              value: element.MetadataID,
              label: element.MetadataName
            });
          }
        });
      }
    },
    addGauge() {
      var index = this.trees.length + 1;
      const childitem = [
        {
          dr_pj: "",
          dr_method: "",
          dr_unit: "",
          dr_money: "",
          dr_demo: ""
        }
      ];
      const item = {
        label: "子列表" + index,
        isFold: false,
        showHeadOpts: true,
        children: childitem
      };
      this.trees.push(item);
    },
    titleRemove(i) {
      this.trees.splice(i, 1);
    },
    async SaveGauge() {
      this.SyncLoading = true;
      const res = await api.Repair.UpdatePJ(
        this.drid,
        JSON.stringify(this.trees).toString()
      );
      if (res) {
        this.SyncLoading = false;
      }
      if (!res.Result) {
        this.$message({
          showClose: true,
          message: "保存失败",
          type: "error"
        });
      } else {
        this.$message({
          showClose: true,
          message: "保存成功",
          type: "success"
        });
      }
    }
  }
};
</script>
<style lang="less">
.dr-tree {
  width: 100%;
  .dr-head {
    width: 100%;
    height: 32px;
    font-size: 0.9rem;
    div {
      height: 100%;
      line-height: 32px;
    }
    .tip {
      padding: 0 12px;
      background: #ebf4ff;
      color: #aaa;
      float: left;
      .title-input {
        width: 136px !important;
        height: 28px !important;
        display: inline-block;
        .el-input__icon {
          line-height: 32px !important;
        }
      }
    }
    .title-input {
      width: 152px;
      float: right;
      margin-right: 12px;
    }
    .add-btn {
      width: 96px;
      float: right;
      color: #fff;
      font-size: 0.8rem;
      text-align: center;
      border-radius: 3px;
      margin-right: 4px;
      cursor: pointer;
      padding: 4px 15px;
    }
  }
  .box {
    width: 100%;
    margin-top: 8px;
    .first-node {
      margin-top: 16px;
      border: 1px solid #ebebeb;
      .first-node-head {
        height: 40px;
        background: #ebebeb;
        padding: 0 12px;
        user-select: none;
        div {
          height: 100%;
          line-height: 40px;
        }
        .title {
          float: left;
          font-size: 1rem;
          color: #888888;
          .iconfont {
            font-size: 1.52rem !important;
            cursor: pointer;
          }
          .title-input {
            display: flex;
            width: 150px;
            float: right;
          }
        }
        .opts {
          width: 68px;
          float: right;
          display: flex;
          justify-content: space-around;
          align-items: center;
          .iconfont:hover {
            cursor: pointer;
          }
          .icon-tianjiazijiedian {
            font-weight: bold;
          }
        }
      }
      .dr-container {
        width: 100%;
        padding-bottom: 8px;
        .second-node {
          width: 97%;
          padding: 8px 10px;
          margin: 0 auto;
          margin-top: 8px;
          background: white;
          display: flex;
          justify-content: space-around;
          align-items: center;
          .iconfont {
            user-select: none;
            cursor: pointer;
          }
          strong {
            display: inline-block;
            width: 136px;
          }
        }
      }
    }
  }
}
</style>
