<template>
  <div class="personal-bottom-pager">
    <el-pagination
      v-loading="Loading"
      :current-page.sync="PIndex"
      :page-sizes="PageSizes"
      :page-size.sync="PSize"
      :total="TotalCounts"
      @size-change="PageSizeChanged"
      @current-change="PageChanged"
      layout="total, sizes, prev, pager, next, jumper"
    />
  </div>
</template>
<script>
export default {
  props: {
    // 加载中
    Loading: {
      type: Boolean,
      default: false
    },
    // 当前第几页
    PageIndex: {
      type: Number,
      default: 1
    },
    PageSizes: {
      type: Array,
      default: () => [10, 20, 30, 40, 50]
    },
    // 分页大小
    PageSize: {
      type: Number,
      default: 10
    },
    // 总行数
    TotalCounts: {
      type: Number,
      default: 0
    }
  },
  watch: {
    $route: function(newRouter, oldRouter) {
      this.$emit("update:PageIndex", 1);
      this.$emit("LoadData");
    }
  },
  computed: {
    PIndex: {
      get() {
        return this.PageIndex;
      },
      set() {
        this.$emit("update:PageIndex", this.PageIndex);
      }
    },
    PSize: {
      get() {
        return this.PageSize;
      },
      set() {
        this.$emit("update:PageSize", this.PageSize);
      }
    }
  },
  data() {
    return {};
  },
  methods: {
    /**
     * 分页大小改变时会触发
     */
    PageSizeChanged(val) {
      this.$emit("update:PageIndex", 1);
      this.$emit("update:PageSize", val);
      this.$emit("LoadData");
    },
    /**
     * 切换分页时触发
     * @param {number} val 新的页码
     */
    PageChanged(val) {
      console.log(val);
      this.$emit("update:PageIndex", val);
      this.$emit("update:PageSize", this.PageSize);
      this.$emit("LoadData");
    }
  }
};
</script>
<style lang="less">
