.el-pagination {
  @FontColor: #888;

  float: right;

  button,
  .el-pager li {
    font-size: 12px;
    color: #999999;
    background-color: #ffffff;
    border: 1px solid #dddddd;
  }

  button:not(:disabled):hover,
  .el-pager li:hover {
    color: #fff;
    background-color: fade(@ThemeColor, 60%);
    border-color: fade(@ThemeColor, 60%);
  }

  button:disabled {
    color: #bbb;
    border: 1px solid #ddd;
    background-color: #f7f7f7;
    i {
      cursor: not-allowed;
    }
  }

  .el-pager {
    li:first-child {
      border-left: none;
    }

    li + li {
      border-right: none;
    }

    li.active {
      color: #ffffff;
      cursor: default;
      background-color: @ThemeColor;
      border: 1px solid @ThemeColor;
    }
  }

  .btn-prev,
  .btn-next {
    i {
      display: block;
    }
  }

  .btn-prev {
    padding-right: 6px;
  }

  .btn-next {
    padding-left: 6px;
  }

  button,
  span:not([class*="suffix"]) {
    font-size: 12px;
  }

  .el-pagination__jump {
    .el-input {
      margin-left: 5px;
      margin-right: 5px;
      font-size: 12px;
    }
  }

  .el-pagination__total {
    color: @FontColor;
  }

  .el-pagination__sizes {
    .el-input {
      .el-input__inner {
        font-size: 12px;
        padding-right: 22px;
        color: @FontColor;
      }
    }
  }
}
</style>
