<template>
  <div class="student-question">
    <el-tabs v-model="questionTabSelected" @tab-click="GetStudentQuestion">
      <el-tab-pane v-for="(item, index) in tabOptions" :key="index" :label="item.label"
                   :name="item.index">
        <el-row v-if="item.dataList && item.dataList.length>0" v-loading="listLoading">
          <el-col :span="18">
            <!-- 提问记录 -->
            <div class="chat" ref="chat">
              <div class="chat_content">
                <div v-for="(sub, k) in item.dataList" :key="k"
                     :class="sub.studentID !== selectStudentID ? 'chat_item' : 'chat_item checked_item'">
                  <div class="chat_news">
                    <div class="chat_news_person">
                      <img src="" v-real-img="[sub.userID, sub.studentID]" alt/>
                      <div class="chat_news_name">
                        <div>{{sub.studentName || "未知人员"}}</div>
                        <div class="time">{{formatDate(sub.createTime, 'yyyy-MM-dd hh:mm:ss')}}</div>
                      </div>
                    </div>
                    <div class="chat_news_cont">
                      <span>{{sub.content}}</span>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </el-col>
          <el-col :span="6">
            <div class="summary">提问人员</div>
            <div class="list_person">
              <div v-for="(sub, index) in item.dataPerson" :key="index" class="person_item"
                   :class="selectStudentID === sub.studentID ? 'person_selected' : ''"
                   @click="selectStudentID = sub.studentID">
                <img src="" v-real-img="[sub.userID, sub.studentID]" alt/>
                <span>{{sub.studentName}}</span>
                <span></span>
              </div>
            </div>
          </el-col>
        </el-row>
        <div v-else class="no-q" style="text-align: center;">暂无提问</div>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import { FormatDate } from "@/scripts/utils";
import api from "@/scripts/api";

export default {
  name: "StudentQuestion",
  props: {
    classHour: {
      type: Object
    }
  },
  data () {
    return {
      dataList: [],
      studentQuestion: [],
      selectStudentID: "",
      listLoading: false,

      questionList: [],
      questionTabOptions: [],
      questionTabSelected: ""
    };
  },
  computed: {
    tabOptions () {
      return this.questionTabOptions.filter((a, b) => b > 0);
    }
  },
  created () {
    this.courseQuestion();
  },
  methods: {
    formatDate (dateValue, format) {
      return FormatDate(dateValue, format);
    },
    courseQuestion () {
      this.questionTabOptions = [];
      const state = this.classHour.state;
      this.questionTabOptions = [
        {
          index: "0",
          attribute: 1,
          label: "预习",
          disabled: true,
          dataList: [],
          dataPerson: []
        },
        {
          index: "1",
          attribute: 2,
          label: "课中",
          disabled: true,
          dataList: [],
          dataPerson: []
        },
        {
          index: "2",
          attribute: 3,
          label: "复习",
          disabled: false,
          dataList: [],
          dataPerson: []
        }
      ];
      this.questionTabSelected = this.questionTabOptions[state].index;
      this.GetStudentQuestion();
    },
    /**
     * 获取学生提问
     * @constructor
     */
    GetStudentQuestion () {
      const questionTab = this.questionTabOptions[this.questionTabSelected];
      questionTab.dataList = [];
      questionTab.dataPerson = [];
      const dataList = [];
      const dataPerson = [];
      this.listLoading = true;
      api.IQuestion.ClassHourDataList(this.classHour.id, this.questionTabOptions[this.questionTabSelected].attribute).then(res => {
        if (res.Result && res.Datas && res.Datas.length > 0) {
          res.Datas.forEach(item => {
            const sub = {
              studentID: item.Key["StudentID"],
              userID: item["UserID"],
              studentNo: item["sno"],
              studentName: item["StudentName"],

              id: item.Key["CIID"],
              content: item.Key["QContent"],
              state: item.Key["State"],
              createTime: item.Key["CreateTime"],
              attribute: item.Key["Attribute"]
            };
            dataList.push(sub);

            const index = dataPerson.findIndex(r => r.studentID === item.Key["StudentID"]);
            if (index === -1) {
              dataPerson.push({
                studentID: item.Key["StudentID"],
                userID: item["UserID"],
                studentNo: item["sno"],
                studentName: item["StudentName"]
              });
            }
          });
          questionTab.dataList = dataList;
          questionTab.dataPerson = dataPerson;
        }
        this.listLoading = false;
      }).catch(() => {
        this.listLoading = false;
      })
    }
  },
  watch: {
    classHour: {
      immediate: true,
      handler: "courseQuestion"
    }
  }
};
</script>

